body {
  background: #FFF;
  font-family: 'CharterItalic';
  font-size: 16px;
  color: $brown;
  line-height: 1.4; }

.dots {
    display: block;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    bottom: get-vh(30px);
    .dot__wrapper {
        display: inline-block;
        border: 1px solid transparent;
        border-radius: 50%;
        cursor: pointer;
        &:hover {
            transition: 0.4s;
            border-color: $gold; }
        &.active {
            border-color: $gold; }
        .dot {
            width: 15px;
            height: 15px;
            margin: 5px;
            border: none;
            border-radius: 50%;
            background-color: $gold;
            @include md {
                width: 13px;
                height: 13px; }
            @include sm {
                width: 11px;
                height: 11px; }
            @include xs {
                width: 9px;
                height: 9px; } } } }

.btn {
    display: block;
    background-color: transparent;
    border: 1px solid $gold;
    padding: 10px 20px;
    font-family: $ptsans-narrow-bold;
    color: $gold;
    text-transform: uppercase;
    &:hover {
        transition: 0.4s;
        background-color: $gold;
        color: #fff; } }

.dropdown-list {
    width: 100%;
    background-color: #fff;
    color: $gold;
    padding-left: 42px;
    .dropdown-list__item {
        @include vertical;
        text-align: left;
        font-size: 1em;
        height: 45px; } }

@each $png-icon in $png-icons {
    .icon__#{$png-icon} {
        display: inline-block;
        @include s($png-icon); } }
