.map {
    position: relative;
    .map__canvas {
        width: 100%;
        height: 590px;
        position: relative;
        z-index: 1; }
    .map__controls {
        width: 100%;
        max-width: 1074px;
        height: 45px;
        position: absolute;
        top: 50px;
        left: 50%;
        right: 50%;
        transform: translate(-50%, -50%);
        z-index: 999;
        background-color: transparent;
        @include clr;
        .map__scale {
            max-width: 20px;
            margin-right: 8px;
            background-color: transparent;
            .map__zoom-in {
                margin-bottom: 5px; } }
        .map__scale > * {
            width: 20px;
            height: 20px;
            background-color: $gold;
            cursor: pointer; }
        .map__on-map {
            max-width: 175px;
            @include r(1075) {
                max-width: 125px; }
            @include r(930) {
                max-width: 100px; }
            @include r(777) {
                display: none; }
            .map__on-map-text {
                @include vertical;
                font-size: 1em;
                overflow: hidden; }
            .map__on-map-icon {
                @include vertical;
                font-size: 1.3em;
                .icon-marker {
                    vertical-align: middle;
                    fill: #fff; } } }
        .map__list {
            max-width: 530px;
            background-color: #fff;
            text-align: left;
            cursor: pointer;
            @include r(1075) {
                max-width: 480px; }
            @include r(930) {
                max-width: 380px; }
            @include r(777) {
                display: none; }
            .map__list-text {
                @include vertical;
                font-size: 1em;
                color: $gold; }
            .map__list-icon {
                @include vertical;
                font-size: 1.3em;
                padding-left: 42px;
                .icon-list {
                    vertical-align: middle;
                    fill: $gold; } } }
        .map__region {
            max-width: 330px;
            @include r(1075) {
                max-width: 280px; }
            @include r(930) {
                max-width: 250px; }
            @include r(301) {
                max-width: 220px; }
            .map__region-text {
                @include vertical;
                font-size: 1em; } } }
    .map__controls > * {
        width: 100%;
        height: 100%;
        background-color: $gold;
        font-family: $ptsans-narrow-bold;
        color: #fff;
        text-align: center;
        text-transform: uppercase;
        float: left; } }



